<template>
  <div class="admin-global-search-table">
    <div class="admin-global-search-table__filters">
      <CheckButton v-model="filters.pinned">
        <div class="pinned-btn">
          <Icon name="pin" />
          Pinned
          <div class="pinned-btn__count">
            {{ count }}
          </div>
        </div>

      </CheckButton>
      <CheckButton v-model="filters.results">
        Results
      </CheckButton>
    </div>
    <Table
      :columns="columns"
      :data="data"
    >
      <template #column="{ column }">
        {{ t(column.title) }}
      </template>
      <template #cell="{ row, item, column }">
        <PinCheck
          v-if="column.type === TABLE_TYPES.PIN"
          v-model="row[column.name]"
        />
        <Badge
          v-else-if="column.type === TABLE_TYPES.STATUS"
          :type="COLORS[item]"
        >
          <Text v-if="STATUS[item]" color="inherit" weight="400">{{ t(STATUS[item]) }}</Text>
        </Badge>
        <Date
          v-else-if="column.type === TABLE_TYPES.DATE"
          format="DD MMM YYYY"
          :date="item"
        />
        <Date
          v-else-if="column.type === TABLE_TYPES.TIME"
          format="HH:mm A"
          :date="item"
        />
        <Text type="uppercase" v-else-if="column.type === TABLE_TYPES.CRYPTO_PAIR">
          {{ (item || [])[0] }} > {{ (item || [])[1] }}
        </Text>
        <Text type="uppercase" v-else-if="column.type === TABLE_TYPES.CURRENCY">
          {{ item }}
        </Text>
        <span v-else>{{ item }}</span>
      </template>
    </Table>
  </div>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useGetters } from '@/shared/plugins/vuex'
import { TABLE_TYPES } from '@/admin/common/tableTypes'
import { MODULES_NAMES } from '@/shared/common/modules'
import { STATUS, COLORS } from '@/shared/common/status'
import Date from '@/shared/components/UIKit/Text/Date'
import Badge from '@/shared/components/UIKit/Badge'
import Table from '@/admin/components/Table/Table'
import CheckButton from '@/admin/components/CheckButton'
import { reactive, ref } from 'vue'
import PinCheck from '@/admin/components/PinCheck'
import Text from '@/shared/components/UIKit/Text/Text'
import Icon from '@/shared/components/Icon/Icon'

export default {
  name: 'AdminGlobalSearch',
  components: { Icon, Text, PinCheck, CheckButton, Date, Badge, Table },
  setup () {
    const { t } = useI18n()
    const {
      getTableData: data,
      getTableColumns: columns
    } = useGetters(MODULES_NAMES.GLOBAL_SEARCH)

    const count = ref(11)
    const filters = reactive({
      pinned: true,
      results: false
    })

    return {
      t,
      data,
      count,
      STATUS,
      COLORS,
      filters,
      columns,
      TABLE_TYPES
    }
  }
}
</script>

<style lang="scss">
.admin-global-search-table {
  margin: 20px 20px 0 40px;

  &__filters {
    display: flex;
    margin-bottom: 20px;

    .pinned-btn {
      display: flex;
      align-items: center;

      .icon {
        margin-right: 9px;
      }
      &__count {
        background-color: $adminErrorColor;
        color: white;
        padding: 4px 4px;
        margin-left: 15px;
        border-radius: 50%;

        font-size: 12px;
      }
    }
  }
  .table__row__cell-wrap {
    font-weight: 500;
  }
}
</style>
